$bg-clr: #ffffff;
$anim-main-bg-clr: white;
html, body{
  box-sizing: border-box;
}

.main-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: var(--bg-clr, $bg-clr);
}
.main-animation{
  animation-name: main-loading-anim;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation-delay: 0s;
}
@keyframes main-loading-anim {
  100%{
    
    background-color: var(--anim-main-bg-clr, $anim-main-bg-clr);}
}